<template>
  <div class="hello">
    <div class="header-box"
         @click="goLogin"
         v-if="!userName">
      <router-link :to="{name:'login'}">
        <div class="header-box-img">
          <img src="data:image/png;base64,"
               alt="为登录头像">
        </div>
        <p class="header-box-text">登录</p>
      </router-link>
    </div>
    <div class="header-box"
         v-else>
      <div class="header-box-img">
        <img src="data:image/png;base64,"
             alt="为登录头像">
      </div>
      <p class="header-box-text">{{userName}}</p>
    </div>

    <div class="main-box">
      <div class="main-box-nav">
        <div class="main-box-nav-item"
             v-for="item in navList"
             :key="item.index">
          <div class="main-box-nav-item-img">
            <img :src="item.src">
          </div>
          <p class="main-box-nav-item-text">{{item.desc}}</p>
        </div>
      </div>
      <div class="ui-gray"></div>

      <div class="main-box-list">
        <div class="main-box-list-item"
             v-for="(item,index) in optList"
             :key="item.index"
             @click="goTowhere(index)">
          <div class="main-box-list-item-img">
            <img :src="item.src">
          </div>
          <p class="main-box-list-text">{{item.desc}}</p>
          <img class="main-box-list-item-right"
               src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAYBAMAAAA46dFkAAAABGdBTUEAALGPC/xhBQAAAA9QTFRFAAAA////payzpKu0pKqzD7zVUAAAAAR0Uk5TAAFKnTZ5RNsAAAAsSURBVAjXYxBSZAADFWcBCO1iCKaFXSACjCYUCSDTMHGoOpK4cHfB3Al1NwBi6Q5b1PFeOQAAAABJRU5ErkJggg==">
        </div>
      </div>
      <button type="button"
              class="clear-app-message"
              @click="clearApp">清空APP信息</button>

      <button type="button"
              class="clear-app-message"
              @click="refresh">刷新</button>
    </div>
    <el-footer class="footer-box">
      <div class="footer-item footer-item1">
        <router-link :to="{name:'index'}">
          <div class="footer-item-icon"><img src="data:image/png;base64,"></div>
          <p class="footer-item-text">首页</p>
        </router-link>
      </div>
      <div class="footer-item footer-item2">
        <router-link :to="{name:'classify'}">
          <div class="footer-item-icon"><img src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADwAAAA8BAMAAADI0sRBAAAAFVBMVEVHcEzIyMjDw8PDw8PDw8PDw8PDw8NNc7pKAAAAB3RSTlMAHKbm/+2nMPnTbQAAAE1JREFUeAFjIBuMAkZlFxAwEkBmIoCoCwQEIjMRIAUq5oTMRAATqJgzMhMBXGAAiYlDelSacKASjhLCEcqYDEsDCOYwT6mjKXU0pY4CAF0upynPNQ7eAAAAAElFTkSuQmCC"></div>
          <p class="footer-item-text">分类</p>
        </router-link>
      </div>
      <div class="footer-item footer-item3">
        <router-link :to="{name:'cart'}">
          <div class="footer-item-icon"><img src="data:image/png;base64,"></div>
          <p class="footer-item-text">购物车</p>
        </router-link>
      </div>
      <div class="footer-item footer-item4">
        <router-link :to="{name:'mine'}">
          <div class="footer-item-icon"><img src="data:image/png;base64,"></div>
          <p class="footer-item-text">我的</p>
        </router-link>

      </div>
    </el-footer>
  </div>
</template>

<script>

export default {
  name: 'mine',
  data () {
    return {
      navList: [
        { 'index': 1, desc: "待付款", src: "data:image/png;base64," },
        { 'index': 2, desc: "待收货", src: "data:image/png;base64," },
        { 'index': 3, desc: "待发货", src: "data:image/png;base64," },
        { 'index': 4, desc: "待评价", src: "data:image/png;base64," }
      ],
      optList: [
        { index: 1, desc: "注册", src: "data:image/png;base64," },
        { index: 2, desc: "转账", src: "data:image/png;base64," },
        { index: 3, desc: "设置SDK信息", src: "data:image/png;base64," },
      ],
      userName: ""
    }
  },
  methods: {
    goTowhere (index) {
      if (index == 0) {
        this.$router.push({ path: '/mine/rigister' })
      }
      if (index == 1) {
        this.$router.push({ path: '/detail/transfer' })
      }
      if (index == 2) {
        this.$router.push({ path: '/mine/setsdk' })
      }
    },
    goLogin () {
      // 去登录按钮的埋点
      AnalysysAgent.track("btn_click", {
        "btn_name": "登录",
        "page_name": "我的"
      })
    },
    clearApp () {
      // 清空 sdk
      AnalysysAgent.reset();
      // 清空本地存储
      localStorage.removeItem("AnsUserName");
      localStorage.removeItem("AnsIsLogin");
      localStorage.removeItem("AnsUserSex");
      this.userName = ""
    },
    refresh () {
      location.reload();
    }
  },
  mounted: function () {
    // 判断是都登录
    let userName = localStorage.getItem("AnsUserName")
    let userSex = localStorage.getItem("AnsUserSex")
    if (userName) {
      this.userName = userName
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ui-gray {
  width: 100%;
  height: 20px;
  background: #eee;
}
.header-box {
  width: 100%;
  height: 200px;
  padding-top: 40px;
  box-sizing: border-box;
  background: linear-gradient(90deg, #fa800a, #fa800a);
}
.header-box-img {
  width: 72px;
  height: 72px;
  margin: 0 auto 0;
}
.header-box-img img {
  display: block;
  width: 100%;
  height: 100%;
}
.header-box-text {
  height: 40px;
  line-height: 80px;
  color: #fff;
}
/* 中间导航 */
.main-box-nav {
  display: flex;
  height: 70px;
}
.main-box-nav-item {
  flex: 1;
}
.main-box-nav-item-img {
  width: 28px;
  height: 28px;
  margin: 10px auto 0;
}
.main-box-nav-item-img img {
  display: block;
  width: 100%;
  height: 100%;
}
.main-box-nav-item-text {
  font-size: 14px;
  line-height: 36px;
}
.main-box-list-item {
  overflow: hidden;
  border-bottom: 1px solid #eee;
  height: 46px;
  line-height: 46px;
}
.main-box-list-item-img {
  float: left;
  width: 28px;
  height: 28px;
  margin-left: 20px;
  margin-top: 10px;
}
.main-box-list-item-img img {
  display: block;
  width: 100%;
  height: 100%;
}
.main-box-list-text {
  float: left;
  text-align: left;
  text-indent: 20px;
}
.main-box-list-item-right {
  float: right;
  width: 7px;
  height: 12px;
  margin-right: 12px;
  margin-top: 17px;
}
/* 清空app信息 */
.clear-app-message {
  width: 70%;
  height: 40px;
  border: none;
  outline: none;
  background: #fa800a;
  color: #fff;
  border-radius: 5px;
  margin-top: 40px;
  font-size: 16px;
}

/*底部导航 */
.footer-box {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #eee;
  background: #fff;
}
/* 底部导航 */
.el-footer {
  border-top: 1px solid #ccc;
  background-color: #fefefe;
  color: #333;
  text-align: center;
  line-height: 60px;
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 0;
}
.footer-item {
  flex: 1;
}
.footer-item1 {
  flex: 0.8;
}
.footer-item4 {
  flex: 0.8;
}
.footer-item-icon {
  width: 30px;
  height: 30px;
  margin: 5px auto 0;
}
.footer-item-icon img {
  display: block;
  width: 100%;
  height: 100%;
}
.footer-item-text {
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #333;
}
a {
  text-decoration: none;
}
.router-link-exact-active p {
  color: #fa800a;
}
</style>
<style>
.el-carousel__indicators--horizontal {
  bottom: -77px !important;
}
</style>



