<template>
  <div class="hello">
    <header class="ui-header">
      <div class="ui-header-back"
           @click="goBack">
        <img src="data:image/png;base64,">
      </div>
      <p class="ui-header-text">注册</p>
    </header>
    <div class="ui-name">
      <div class="ui-name-icon">
        <img src="data:image/png;base64,"
             alt="">
      </div>
      <input v-model="name"
             type="text"
             class="input"
             placeholder="请输入用户名"></input>
    </div>
    <div class="ui-password">
      <div class="ui-password-icon">
        <img src="data:image/png;base64,"
             alt="">
      </div>
      <input v-model="password"
             type="password"
             class="input"
             placeholder="请输入密码"></input>
    </div>
    <div class="ui-password">
      <div class="ui-password-icon">
        <img src="data:image/png;base64,">
      </div>
      <div class="sex-radio">
        <input v-model="gender"
               type="radio"
               value="0"
               name="sex">女</input>
      </div>
      <div class="sex-radio">
        <input v-model="gender"
               type="radio"
               value="1"
               name="sex">男</input>
      </div>
    </div>
    <div class="ui-gray"></div>
    <button class="login"
            @click="rigister">注册</button>

  </div>
</template>

<script>
export default {
  name: 'rigister',
  data () {
    return {
      name: '',
      password: "",
      gender: ""
    }
  },
  methods: {
    goBack () {
      history.back();
    },
    rigister () {
      if (this.name == "" || this.password == "") {
        console.log("账号或者用户名不能为0")
        return;
      }
      console.log(this.gender)
      this.$store.commit('userName', this.name)
      this.$store.commit('userSex', this.gender)
      //   存储在本地
      localStorage.setItem("AnsUserName", this.name)
      localStorage.setItem("AnsUserSex", this.gender)
      //   注册信息埋点
      AnalysysAgent.track("register", {
        "is_success": true
      }, function () {
        //   注册成功的跳转
        this.$router.push({ path: '/mine' })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body {
  background: #eee;
}
.ui-gray {
  width: 100%;
  height: 20px;
  background: #eee;
}
/* 头部返回 */
.ui-gray {
  width: 100%;
  height: 20px;
  background: #eee;
}
.ui-header {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #ccc;
}
.ui-header-back {
  position: absolute;
  width: 100px;
  height: 50px;
  left: 0;
}
.ui-header-back img {
  display: block;
  width: 24px;
  height: 24px;
  margin-top: 12px;
  margin-left: 12px;
}
.ui-header-text {
  text-align: center;
  height: 50px;
  line-height: 50px;
  color: #fa800a;
}
.ui-name {
  overflow: hidden;
  border-bottom: 1px solid #eee;
  height: 40px;
}
.ui-password {
  overflow: hidden;
  height: 40px;
  border-bottom: 1px solid #eee;
}
.ui-name-icon {
  float: left;
  width: 40px;
  height: 40px;
  margin-left: 10px;
}
.ui-name-icon img {
  display: block;
  width: 24px;
  height: 24px;
  margin: 8px auto 0;
}
.ui-password-icon {
  float: left;
  width: 40px;
  height: 40px;
  margin-left: 10px;
}
.ui-password-icon img {
  display: block;
  width: 24px;
  height: 24px;
  margin: 8px auto 0;
}
.input {
  width: 200px;
  border: none;
  outline: none;
  height: 40px;
  font-size: 14px;
  float: left;
  margin-left: 10px;
}
.login {
  width: 90%;
  height: 45px;
  color: #fff;
  border-radius: 5px;
  outline: none;
  border: none;
  margin-top: 40px;
  background: #fa800a;
  font-size: 18px;
  font-weight: 600;
}
.sex-radio {
  float: left;
  margin-left: 30px;
  margin-top: 9px;
}
</style>
<style>
.el-carousel__indicators--horizontal {
  bottom: -77px !important;
}
</style>



