<template>
  <div class="hello">
    <header class="ui-header">
      <div class="ui-header-back"
           @click="goBack">
        <img src="data:image/png;base64,">
      </div>
      <p class="ui-header-text">登录</p>
    </header>
    <div class="ui-name">
      <div class="ui-name-icon">
        <img src="data:image/png;base64,">
      </div>
      <input v-model="name"
             type="text"
             class="input"
             placeholder="请输入用户名"></input>
    </div>
    <div class="ui-password">
      <div class="ui-password-icon">
        <img src="data:image/png;base64,">
      </div>
      <input v-model="password"
             type="password"
             class="input"
             placeholder="请输入密码"></input>
    </div>
    <div class="ui-gray"></div>
    <button class="login"
            @click="login">登录</button>
    <router-link :to="{name:'rigister'}">
      <p class="go-rigister">没有账号？去注册</p>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      name: '',
      password: "",
    }
  },
  methods: {
    goBack () {
      history.back();
    },
    login () {
      if (this.name == "" || this.password == "") {
        console.log("账号或者用户名不能为0")
        return;
      }
      this.$store.commit('userName', this.name)
      //   存储在本地
      localStorage.setItem("AnsUserName", this.name)
      //   登录信息埋点
      AnalysysAgent.track("login_in", {
        "login_method": "账号密码",
        "is_success": true
      })
      //   发送关联
      AnalysysAgent.alias(this.name, function () {
        localStorage.setItem("AnsIsLogin", true)
      })
      this.$router.push({ path: '/mine' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html,
body {
  background: #eee;
}
.ui-gray {
  width: 100%;
  height: 20px;
  background: #eee;
}
/* 头部返回 */
.ui-gray {
  width: 100%;
  height: 20px;
  background: #eee;
}
.ui-header {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #ccc;
}
.ui-header-back {
  position: absolute;
  width: 100px;
  height: 50px;
  left: 0;
}
.ui-header-back img {
  display: block;
  width: 24px;
  height: 24px;
  margin-top: 12px;
  margin-left: 12px;
}
.ui-header-text {
  text-align: center;
  height: 50px;
  line-height: 50px;
  color: #fa800a;
}
.ui-name {
  overflow: hidden;
  border-bottom: 1px solid #eee;
  height: 40px;
}
.ui-password {
  overflow: hidden;
  height: 40px;
}
.ui-name-icon {
  float: left;
  width: 40px;
  height: 40px;
  margin-left: 10px;
}
.ui-name-icon img {
  display: block;
  width: 24px;
  height: 24px;
  margin: 8px auto 0;
}
.ui-password-icon {
  float: left;
  width: 40px;
  height: 40px;
  margin-left: 10px;
}
.ui-password-icon img {
  display: block;
  width: 24px;
  height: 24px;
  margin: 8px auto 0;
}
.input {
  width: 200px;
  border: none;
  outline: none;
  height: 40px;
  font-size: 14px;
  float: left;
  margin-left: 10px;
}
.login {
  width: 90%;
  height: 45px;
  color: #fff;
  border-radius: 5px;
  outline: none;
  border: none;
  margin-top: 40px;
  background: #fa800a;
  font-size: 18px;
  font-weight: 600;
}
/* 去注册 */
.go-rigister {
  width: 90%;
  text-align: right;
  line-height: 40px;
  font-size: 14px;
  color: #fa800a;
}
.sex-radio {
  float: left;
  margin-left: 30px;
  margin-top: 9px;
}
</style>
<style>
.el-carousel__indicators--horizontal {
  bottom: -77px !important;
}
</style>



